
<template>

    <div>
        <el-tag type="warning"  style="margin-left: 20px;margin-bottom: 10px;width: 200px;height: 40px;text-align: center;line-height: 40px;font-size: 20px">供应商订单列表</el-tag>
        <el-col :span="24" style=" padding-bottom: 30px;">

            <el-row>

                <el-col :span="11" class="el-col" style="width: 500px">
                    <p class="input-title">创建时间:</p>
                    <el-date-picker
                        style="float: left"
                        v-model="searchForm.startCrtTime"
                        type="datetime"
                        placeholder="开始时间"
                        value-format="yyyy-MM-dd HH:mm:ss"
                       >
                    </el-date-picker>
                    <span style="float: left;line-height: 40px">&nbsp;&nbsp;至&nbsp;&nbsp;</span>
                    <el-date-picker
                        style="float: left"
                        v-model="searchForm.endCrtTime"
                        type="datetime"
                        placeholder="结束时间"
                        value-format="yyyy-MM-dd HH:mm:ss"

                    >
                    </el-date-picker>
                </el-col>

                <el-col :span="11" class="el-col" style="width: 500px">
                    <p class="input-title">出发时间:</p>
                    <el-date-picker
                        style="float: left"
                        v-model="searchForm.startDepTime"
                        type="datetime"
                        placeholder="开始时间"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        >
                    </el-date-picker>
                    <span style="float: left;line-height: 40px">&nbsp;&nbsp;至&nbsp;&nbsp;</span>
                    <el-date-picker
                        style="float: left"
                        v-model="searchForm.endDepTime"
                        type="datetime"
                        placeholder="结束时间"
                        value-format="yyyy-MM-dd HH:mm:ss"

                    >
                    </el-date-picker>
                </el-col>
                <div style="margin-top: 90px">
                    <el-col :span="5" class="el-col"  style="margin-left: 20px;width: 200px">
                        <p class="input-title">手机号:</p>
                        <el-input  v-model="searchForm.contactPhone" placeholder="请输入手机号">
                        </el-input>
                    </el-col>
                    <el-col :span="6" class="el-col"  style="margin-left: 20px;width: 200px">
                        <p class="input-title">供应商:</p>
                        <el-select v-model="searchForm.supplierId" @change="supplierChange" placeholder="请选择">
                            <el-option
                                v-for="item in supplierOptions"
                                :key="item.supplierId"
                                :label="item.supplierName"
                                :value="item.supplierId">
                            </el-option>
                        </el-select>
                    </el-col>
                    <el-col :span="4" class="el-col"  style="margin-left: 20px;width: 200px">
                        <p class="input-title">出发站点:</p>
                        <el-select v-model="searchForm.depStation"  placeholder="请选择">
                            <el-option
                                v-for="item in depStationlist"
                                :key="item.depStation"
                                :label="item.depStation"
                                :value="item.depStation">
                            </el-option>
                        </el-select>
                    </el-col>
                </div>
            </el-row>
        </el-col>
        <el-col :span="23" style=" padding-bottom: 30px;border-bottom: 1px solid gainsboro">

            <p class="input-title">订单状态:</p>
            <template>

                <el-checkbox style="display: inline-block;margin-right: 20px"  v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
                <el-checkbox-group  style="display: inline-block" v-model="checkedOrders" @change="handleCheckedCitiesChange">

                    <el-checkbox  style="display: inline-block" v-for="order in orders" :label="order" :key="order">{{orderStatus[order]}}</el-checkbox>
                </el-checkbox-group>
            </template>
            <el-col :span="1.5" class="el-col" style="float: right;margin-top: -50px">
                <p class="input-title">&nbsp;</p>
                <el-button type="primary"   @click = "supOrderExp">导出明细</el-button>
            </el-col>
            <el-col :span="1.5" class="el-col" style="float: right;margin-top: -50px">
                <p class="input-title">&nbsp;</p>
                <el-button type="primary"   @click = "clear">重置</el-button>
            </el-col>
            <el-col :span="1.5" class="el-col" style="float: right;margin-top: -50px">
                <p class="input-title">&nbsp;</p>
                <el-button type="primary" icon="el-icon-search" v-loading.fullscreen.lock="fullscreenLoading" @click = "search">查询</el-button>
            </el-col>

        </el-col>

        <el-col :span="23" style=" padding-top: 30px;">
            <table class="content-table">
                <tr>
                    <th>订单号</th>
                    <th>供应商订单号</th>
                    <th>供应商</th>
                    <th>出发/到达地</th>
                    <th>手机号</th>
                    <th>订单状态</th>
                    <th>订单金额</th>
                    <th>创建日期</th>
                    <th>出发日期</th>
                </tr>
                <tr v-for="(item,index) in listdata.data">
                    <td>
                        <span class="dothis" @click="godetail(item.serialId,item.status)"> {{item.serialId}}</span>
                    </td>
                    <td>{{item.supSerialId}}</td>
                    <td>{{item.supplierName}}</td>
                    <td>{{item.depStation}}/{{item.destStation}}</td>
                    <td>{{item.contactPhone}}</td>
                    <td>{{item.statusText}}</td>
                    <td>{{item.ticketPrice}}</td>
                    <td>{{item.crtTime}}</td>
                    <td>{{item.depTime}}</td>
                </tr>
                <tr>
                    <td colspan="9" style="text-align: left;background: ghostwhite">
                        &nbsp;   &nbsp;   &nbsp;
                        订单总金额 : {{listdata.totalAmount}}元，&nbsp;
                        退款金额 : {{listdata.refundAmount}}元，&nbsp;
                        有效订单 : {{listdata.effectAmount}}元,&nbsp;
                        成人: {{listdata.adultCount}}位,&nbsp;
                        儿童: {{listdata.childCount}}位,&nbsp;
                        退款手续费 : {{listdata.serviceFee}}元,
                    </td>
                </tr>
            </table>
            <div class="block page-f" style="position: relative;width: 100%">

                <el-pagination
                    style="position: absolute;right: 0"
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage"
                    :page-size="pageSize"
                    :page-sizes="[10, 20, 30, 40]"
                    layout="total,prev, pager, next, jumper"
                    :total="listdata.totalCount">
                </el-pagination>
            </div>
            <div style="height: 80px"></div>
        </el-col>


    </div>
</template>
<script>
    import ajax from "../model/sys/index"
    const orderOptions = [1, 2, 3, 4,5,6,7];

    export default {
        data() {
            return {
                checkAll: false,
                checkedOrders: [],
                orders: orderOptions,
                timeValue: '',
                currentPage: 1,//默认页码
                pageSize:10,//每页显示条数
                listdata:"",
                searchForm: {
                    contactPhone:"",
                    endCrtTime:"",
                    startCrtTime:"",
                    status:[],
                    supplierId:"",
                    startDepTime:"",
                    endDepTime:"",
                    depStation:""
                },
                formLabelWidth: '120px',
                fullscreenLoading:false,
                orderStatus:{
                    1:"待支付",
                    2:"出票中",
                    3:"已出票",
                    4:"出票失败",
                    5:"已出行",
                    6:"退票",
                    7:"已取消"
                },
                supplierOptions:[],
                thisSupplierId:"",
                depStationlist:[]
            };
        },
        methods: {
            handleCheckAllChange(val) {
                this.checkedOrders = val ? orderOptions : [];
            },
            handleCheckedCitiesChange(value) {
                let checkedCount = value.length;
                this.checkAll = checkedCount === this.orders.length;
            },

            getSupListOrderList:function () {

                var data  = this.searchForm
                data.pageIndex=this.currentPage
                data.pageSize=this.pageSize

                for(var i in data){
                    if(String(data[i])==""){
                        delete data[i]
                    }
                }

                ajax.getSupListOrderList(data,res=>{

                    if(res){

                        this.listdata = res.data.result
                    }
                })
            },
            clear:function () {
                this.checkedOrders=[]
                this.checkAll=false
                var data  = this.searchForm
                for(var i in data){
                    data[i]=""
                }
                this.value1=""
                this.value2=""

            },
            search:function () {
                this.searchForm.status=this.checkedOrders.concat()
                this.currentPage = 1
                this.getSupListOrderList ()
            },
            godetail(serial,status){
                window.open("#/gz_order_detail?serial="+serial+"&supplierId="+this.thisSupplierId)


            },
            supOrderExp:function () {
                var orderData  = this.searchForm
                for(var i in orderData){
                    if(String(orderData[i])==""){
                        delete orderData[i]
                    }
                }
                ajax.supOrderInfoExp(orderData)
            },
            addDevice:function () {
                this.dialogFormVisible = true
            },
//            getTimeValue1:function (val) {
//                console.log(val)
//                if(val==null){
//                    this.searchForm.startCrtTime = ""
//                }else {
//                    this.searchForm.startCrtTime = val
//                }
//            },
//            getTimeValue2:function (val) {
//
//                if(val==null){
//                    this.searchForm.endCrtTime = ""
//                }else {
//                    this.searchForm.endCrtTime = val
//                }
//            },
            supplierChange(val){
                this.thisSupplierId = val
            },

            refund:function (data) {
                var refundData= {
                    serial:data
                }
                this.$confirm('是否继续退款操作?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    ajax.refund(refundData,res=>{
                        if(res){
                            this.getOrderList()
                            this.$message({
                                showClose: true,
                                message: '退款成功',
                                type: 'success'
                            });
                        }
                    })
                }).catch(() => {

                });

            },
            handleSizeChange(val) {
                this.pageSize = val
                this.getSupListOrderList ()
            },
            handleCurrentChange(val) {

                this.currentPage = val
                this.getSupListOrderList ()
            },
            getDepStationList(){
                ajax.getDepStationList({},res=>{
                    this.depStationlist = res.data.result

                })
            }
        },
        mounted(){
            ajax.adminuserinfo({token:localStorage.getItem("token")},res=>{

                if(res){
                    this.supplierOptions = res.data.result.suppliers
                    this.searchForm.supplierId = this.supplierOptions[0].supplierId
                    this.thisSupplierId = this.supplierOptions[0].supplierId
                    console.log( this.thisSupplierId)
                    this.getSupListOrderList()
                    this.getDepStationList()
                }
            })
         }
    }
</script>
<style>
    .input-title{
        font-size: 14px !important;
        font-weight: 600;
    }
    .el-col{
        margin-left: 20px;
    }
    ::-webkit-scrollbar-thumb{
        background-color: gainsboro;
    }
    .cell{
        text-align: center;
    }
    #content-main{
        overflow: scroll;
    }
    .el-table{
        overflow: scroll;
    }
    .content-table{
        width: 100%;
        border-collapse:collapse;
        background-color: white;
        text-align: center;
    }
    .content-table th{
        height: 40px;
        border: 1px solid #F2F3F4;
        text-align: center;
        background-color: gainsboro;
    }
    .content-table td{
        height: 40px;
        border: 1px solid #F2F3F4;
    }
    .dothis{
        color: #3C9CFC;
        cursor:default;
        margin-left: 10px;
    }
    .page-f{
        margin-top: 20px;
        float: right;
    }
    .content-table span:hover{
        color: silver;
    }
    .el-col-23{
        width: 99% !important;
    }
</style>



